</template>

<script>
import Swiper from '../../static/js/swiper.min.js'

export default {
	name: 'demo',
	data () {
		return {
			msg:"首页"
		}
	},
	mounted () {
		var swiper = new Swiper('.swiper-container', {
			pagination: '.swiper-pagination',
			slidesPerView: 3.5,
			paginationClickable: true,
			spaceBetween: 15,
			freeMode: true
		});
	}
}
</script>

